import React from 'react';

export default function NoxTitanLogo({ size = 64 }: { size?: number }) {
  return (
    <svg
      width={size}
      height={size * 0.4}
      viewBox="0 0 320 128"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      className="drop-shadow-xl"
      style={{ display: 'block' }}
    >
      {/* Wings - shiny metal gradient */}
      <defs>
        <linearGradient id="wingGradient" x1="0" y1="0" x2="320" y2="0" gradientUnits="userSpaceOnUse">
          <stop stopColor="#e0e0e0" />
          <stop offset="0.5" stopColor="#b8aaff" />
          <stop offset="1" stopColor="#e0e0e0" />
        </linearGradient>
        <radialGradient id="crystalGradient" cx="0.5" cy="0.5" r="0.5" gradientTransform="matrix(0 64 -64 0 160 64)" gradientUnits="userSpaceOnUse">
          <stop stopColor="#c084fc" />
          <stop offset="0.7" stopColor="#a78bfa" />
          <stop offset="1" stopColor="#6d28d9" />
        </radialGradient>
      </defs>
      {/* Left wing */}
      <path d="M20 64 Q80 32 160 64 Q80 96 20 64" fill="url(#wingGradient)" stroke="#b8aaff" strokeWidth="3" />
      {/* Right wing */}
      <path d="M300 64 Q240 32 160 64 Q240 96 300 64" fill="url(#wingGradient)" stroke="#b8aaff" strokeWidth="3" />
      {/* Crystal center */}
      <ellipse cx="160" cy="64" rx="32" ry="48" fill="url(#crystalGradient)" stroke="#a78bfa" strokeWidth="4" />
      {/* Shine effect */}
      <ellipse cx="160" cy="48" rx="12" ry="6" fill="#fff" fillOpacity="0.5" />
      {/* Text - metallic, purple gradient */}
      <text x="50%" y="120" textAnchor="middle" fontSize="40" fontWeight="bold" fontFamily="Montserrat, Arial, sans-serif" fill="url(#wingGradient)" stroke="#a78bfa" strokeWidth="2">NOXTITAN</text>
    </svg>
  );
}
